body, h1, h2, h3, p, button {
  margin: 0;
  padding: 0;
}
body {
  font-family: 'Ubuntu', sans-serif;
  background-color: #f5f4f2;
  color: #000000;
  text-align: center;
  padding: 20px;
}

#logo-text {
  font-family: 'Ubuntu', sans-serif;
  font-size: 80px;
  color: #000000;
  margin-top: 100px;
  margin-left: 5px; 
}

.button-group button{
  font-family: 'Ubuntu', sans-serif;
  text-align: center;
  margin-top: 100px;
  margin-left: 5px; 
}

.back button{
  width: 30px; /* 원하는 너비 설정 */
  height: 30px; /* 원하는 높이 설정 */
  background-color: none; /* 원하는 배경색 설정 */
  border: none; /* 테두리 없애기 */
  border-radius: 0; /* 선택적으로 테두리 반경을 0으로 설정하여 더 부드럽게 만들 수 있습니다. */
  position: fixed;
  top: 10px; /* 원하는 상단 여백 설정 */
  left: 10px; /* 원하는 왼쪽 여백 설정 */
}